import * as React from 'react';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormHelperText from '@mui/material/FormHelperText';
import FormControl from '@mui/material/FormControl';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import { borderColor } from '@mui/system';

export default function SelectLabels({title, options, onChange}) {
  const [val, setVal] = React.useState(options[0]);
  // React.useEffect(() => {}, [val])

  const handleChange = (event) => {
    event.preventDefault()
    setVal(event.target.value)
    onChange(event)
  };
  return (
    <div>
    <FormControl sx={{ m: 1, minWidth: 120, marginLeft: "50px", marginTop: "15px", backgroundColor: "lightgrey", borderRadius: 2}}>
        <Select
          value={val}
          onChange={handleChange}
          displayEmpty
          inputProps={{ 'aria-label': 'Without label' }}
          style={{borderColor: "white"}}
        >
          {options && options.length ? options.map((opt, i) => {
            return <MenuItem key={i} value={opt}>{opt}</MenuItem>
          }): <MenuItem value="">
          <em>None</em></MenuItem>}
        </Select>
        <FormHelperText>{title}</FormHelperText>
      </FormControl>
      </div>
  );
}