import React, { useState, useEffect } from "react";
import { Polyline } from "react-leaflet";

import SelectLabels from "./SelectLabels";
import { Button } from "@mui/material";

import cities from "../algo/cityObjsD49";
import TSPMap from "./TSPMap";
import TSPSolver from "../algo/TSPSolver"

const TSPContainer = () => {
    const algoOptions = ["optimal", "nearestN",  "random", "greedy", "primsMST"]
    const cityOptions = [cities.length, Math.ceil(cities.length/2), 8]
    
    const [nodes, setNodes] = useState(cities.slice(0,cityOptions[0]))
    

    const [tableVisible, setTableVisible] = useState(false)
    const [loggingStep, setLoggingStep] = useState(true) 
    
    const [algorithm, setAlgorithm] = useState(algoOptions[0])

    const [step, setStepNumber] = useState(0)

    const [solver, setSolver] = useState(new TSPSolver(nodes, algoOptions[0], loggingStep))
    const [trigger, setTrigger] = useState(Date.now())
    
    document.onkeydown = checkKey;
    function checkKey(e) {
        // e.preventDefault()
        e = e || window.event;
        if (e.keyCode == '65') { // A key

            console.log("cannot go backwards [yet]")
        }
        else if (e.keyCode == '68') {  // D key
            e.preventDefault()
            solver.next()
            setTrigger(Date.now())
        }
        else if (e.keyCode == '69') {  // E key
            e.preventDefault()
            const stepsLeft = (solver.n - solver.solution.length)
            for (let i=0; i<stepsLeft; ++i){
                solver.next()
            }
            setTrigger(Date.now())
        }
    }

    function resetSolver(){
        setSolver(solver.reset())
    }

    function onAlgoChange(event){
        setSolver(new TSPSolver(nodes, event.target.value, loggingStep))
    }

    function onCityChange(event){
        setNodes(cities.slice(0, Number(event.target.value)))
        setSolver(solver.reset(cities.slice(0, Number(event.target.value))))
        setTrigger(Date.now())
    }

    useEffect(()=>{
    }, [trigger, solver, nodes])

    return  <div style={{backgroundColor: "transparent"}}>
        <div id="legend" style={{display: "flex", width: '100%', backgroundColor: "transparent"}}>
            <Button style={{height: 55, marginLeft: 20, marginTop: 15, color:"black", backgroundColor: "grey"}} 
                onClick={resetSolver} variant="contained">Reset</Button>
            <SelectLabels style={{flex: 1}} title={"algorithm"} options={algoOptions} onChange={onAlgoChange} />
            <SelectLabels style={{flex: 1}} title={"no. cities"} options={cityOptions} onChange={onCityChange} />
            {/* <div style={{flex: 1, marginTop: 15, marginLeft: '60%', fontFamily: "sans-serif", backgroundColor: ""}}> */}
            {/* <img style={{maxHeight: 55}} src="https://cdn3.iconfinder.com/data/icons/ringtone-music-instruments/512/letter-d-key-keyboard-3-512.png" /> */}
            <h2 id="keys" style={{flex: 1, marginLeft: '75%', fontFamily: 'sans-serif'}}> Step Forward <br></br> Jump Forward</h2>
            {/* </div>   */}
            
        </div>
        
        <TSPMap className="" nodes={nodes}>
            {solver.solution && solver.solution.length ? solver.solution.map((twoNodes, i) => {
                const firstNode = twoNodes[0]
                const secondNode = twoNodes[1]
                return <Polyline key={trigger + i} 
                positions={[[nodes[firstNode % nodes.length].lat, nodes[firstNode % nodes.length].long],
                    [nodes[secondNode % nodes.length].lat, nodes[secondNode % nodes.length].long]]}
                color="#48E5C2" />
            }) : ""}
        </TSPMap>
    </div>
}



export default TSPContainer


