    findDistanceBetween(coord1, coord2){
        const node2 = coord1[1] || coord2 
        const node1 = coord1[0] || coord1
        return this.distanceTable[Math.max(node1, node2)][Math.min(node1, node2)]
    }

    addLink2Solution(coords){
        const [pt1, pt2] = coords
        this.log(`  new link: ${pt1}:${this.cityObjs[pt1]["abbr"]} ${pt2}:${this.cityObjs[pt2]["abbr"]}`)
        const ordered = pt1 > pt2 ? [pt1, pt2] : [pt2, pt1]
        const newLinkLen = this.findDistanceBetween(ordered[0], ordered[1])
        this.log(`  increment distance: ${newLinkLen} miles`)
        this.totalDistance += newLinkLen
        this.log(`total distance: ${this.totalDistance}`)
        this.solution.push(ordered)
        this.currentStep++
        this.updatePossibleChoicesOnNewCoords(ordered[0], ordered[1])
        if (this.currentStep >= this.n) {
            this.next = () => console.log("solver has completed at", this.currentStep, "steps")
        }
        // this.log(this.timesVisitedDict)
    }



    setNodeToInfinity(coord){
        this.workingTable = this.workingTable.map((row, rowNum) => {
            return row.map((v, colNum) => (colNum === coord || rowNum == coord) ? Infinity : v)
        })
    }

    setEdgeToInfinity(coord1, coord2){    
        const node2 = coord1[1] || coord2 
        const node1 = coord1[0] || coord1
        this.workingTable[Math.max(node1, node2)][Math.min(node1, node2)] = Infinity
    }

    updatePossibleChoicesOnNewCoords(coord1, coord2){
        const node1 = coord1
        const node2 = coord2
        this.timesVisitedDict[node1]++  // update visited Table
        this.timesVisitedDict[node2]++
        this.setEdgeToInfinity(node1, node2) // set edge to infinity
        if (this.timesVisitedDict[node1] === 2){
            this.setNodeToInfinity(node1)
        }
        if (this.timesVisitedDict[node2] === 2){
            this.setNodeToInfinity(node2)
        }
    }



    doesFormSubcycle(ar){
        const finalSteps = this.n
        const startNode = ar[ar.length - 1][0]
        const firstDest = ar[ar.length - 1][1]
        function inner(currentNode, possibleNexts, calls=1) {
            const nextLoc = possibleNexts.find(v => v[0] === currentNode || v[1] === currentNode)
            if (nextLoc) {
                return inner(nextLoc.filter(c => c != currentNode)[0], 
                            possibleNexts.filter(v => v != nextLoc), 
                            calls+1)
            }   
            else {
                if (currentNode === startNode) {
                    if (calls === finalSteps){
                        return false 
                    } else {
                        return true
                    }
                } else {
                    return false
                }
            }
        }
        return inner(firstDest, ar.slice(0,-1))
    }

    // 
    nodeReduce(node, fnk){
        let vals = [...this.workingTable[node], ...this.workingTable.slice(node+1,).map(r => r.slice(node, node+1)[0])]
        const indexChosen = vals.reduce((lastIndex, value, index) => {
            return fnk(vals[lastIndex], value, index) ? index : lastIndex
        }, 0)
        return indexChosen
    }

    // nodes: array, prev - distance, val - distance, index - nodenumbers 
    nodesReduce(nodes, fnk){
        let bests = nodes.map(node => [this.nodeReduce(node, fnk), node].sort().reverse())
        return bests.reduce((lastCoords, nowCoords, index) => {
            return fnk(this.workingTable[lastCoords[0]][lastCoords[1]], 
                    this.workingTable[nowCoords[0]][nowCoords[1]], index) ? nowCoords : lastCoords 
        })
    }

    


