import React, { useState, useEffect }from "react"
import { MapContainer, TileLayer, Marker, CircleMarker, Polyline} from 'react-leaflet'
import L from "leaflet"
import conus from "./conus"

const TSPMap = (props) => {
    
    const { nodes } = props
    const lats = nodes.map(node => node.lat)
    const longs = nodes.map(node => node.long)
    const latAvg = lats.reduce((acc, val) => acc + (val/nodes.length), 0)
    const longAvg = longs.reduce((acc, val) => acc + (val/nodes.length), 0)
    const range = Math.max(Math.max(...lats) - Math.min(...lats), Math.max(...longs) - Math.min(...longs))
    
    // 49 range:  52.4 zoom: 4
    // 7 range: 15, zoom: 6
    const GEOMETRIC_CENTER_CONUS = [latAvg*0.9, longAvg]


    let zoom
    if (range > 40) {zoom = 5}
    if (range <= 30) {zoom=5}
    if (range <= 20) {zoom = 6}
    //let [sol, setSol] = useState(fillSolutionWithCoords(props.solution))

    // if (JSON.stringify(fillSolutionWithCoords(props.solution )) !== JSON.stringify(sol)) {
    //   setSol(fillSolutionWithCoords(props.solution))
    // }


    // convert Solution -> list of pairwise linkages
    // function fillSolutionWithCoords(solutionArray){
    //   return solutionArray.map(sol => {
    //     const c1 = usStates[sol[0]]
    //     const c2 = usStates[sol[1]]
    //     return [[c1.lat, c1.long], [c2.lat, c2.long]]
    //   })
    // }

    

    return (
      <MapContainer id="map" center={GEOMETRIC_CENTER_CONUS} zoomControl={false} zoom={zoom} scrollWheelZoom={true}>
        {/* <TileLayer
          attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        /> */}
        <Polyline positions={conus} color="black" />
        {nodes.map((node, index) => {
          const style = `"width: 300px; color: slategrey; transform: rotate(15deg)"`
          return   <CircleMarker key={index} 
                          center={[node.lat, node.long]} 
                          radius={4}
                          color='black'
                          fillColor="black">
                            <Marker position={[node.lat, node.long]} 
                              icon={L.divIcon({html: `<p style=${style}>${node.id+'.'+node.city}</p>`, 
                                    className: `${index % 2 === 0 ? "even" : "odd"}`, iconSize: [0,0]})}/>
                          </CircleMarker> 
        })}
        {props.children}
      </MapContainer>
    )
}


export default TSPMap